import React, { memo, useState } from 'react';
import  { BatteryWapper } from "./style"
import app from "@/weapps"

const WBattery = memo(() => {

  const [info, setInfo] = useState({})

  return (
    <BatteryWapper>
      <span className="title">geBatteryInfo</span>
      <div className="content-wrapper">
        <div className="item">
          <span className="title">
            当前电量
          </span>
          
          <span className="info">
            {info.level || "未获取"}
          </span>
        </div>
        <div className="content-line"></div>
        <div className="item">
          <span className="title">
            电池模式
          </span>
          <span className="info">
            { (info.isCharging && "正在充电中") ||
              (info.isCharging === false && "电池使用中") ||
              "未获取"
            }
          </span>
        </div>
      </div>
      <button className="button" onClick={
        ()=> {
          app.getBatteryInfo({
            success:(res) => {
              setInfo(res)
            },
            fail: () => {
              console.log("getBatteryInfo fail")
            }
          })
        }
      }>获取电池信息</button>
    </BatteryWapper>
  );
});

export default WBattery;